.experience__container{
    display: block;
}
.experience__container > div{
    background: var(--color-bg-variant) ;
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: (--transition);
}

#experience h2{
    font-size: 2.5rem;
}

.experience__content{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 5rem;
    margin-bottom: 10rem;
}

.experience__details{
    display: flex;
    gap: 1rem;
    transition: all 5s ease;
    border: 2px solid transparent;
    padding: 25px 20px;
    border-radius: 40px;
}
