.experience__details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}


/* ======  MEDIA (MEDIUM)  ====== */
@media screen and (max-width: 1024px){
    .experience__container{
        grid-template-columns: 1fr;
    }
    .experience__container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .experience__content{
        padding: 1rem;
    }
}
@media screen and (min-width: 800px){
    
#experience{
    padding: 200px 0;}
}
/* ======  MEDIA (SMALL)  ====== */
@media screen and (max-width: 835px){
    .experience__container{
        gap: 1rem;
    }
    .experience__container > div h3{
        margin-bottom: 1rem;
    }
    .experience__container > div{
        width: 100%;
        padding: 1rem 1rem;
        margin: 0 auto;
    }
    .experience__content{
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 5rem;
    }
}