footer{
    background: var(--color-bg);
    padding: 3rem 0;
    display: flex;
    justify-content: center;
    gap: 10rem;
    font-size: 0.9rem;
    margin-top:7rem;
}

footer a{
    color: var(--color-bg);
}

.footer__socials{
    display: flex;
    justify-content: center;
    gap: 1.5rem;
}

.footer__socials a{
    background: var(--color-dop);
    color:var(--color-white);
    padding:1.3rem;
    font-size: 1.3rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;

}

.footer__socials a:hover{
    background: var(--color-primary);
}

.footer__copyright{
    align-items: center;
    color: rgba(209,213,232);
    font-size: 1.3rem;
}
/* SMALL */
@media screen and (max-width:600px){
    .permalinks{
        flex-direction: column;
        gap: 1.5rem;
    }
    footer{
        display: block;
        text-align: center;
    }
    .footer__copyright{
        font-size: 1.2rem;
        margin-bottom: 3rem;
    }
    .footer__socials{
        margin-bottom: 2.6rem;
    }
}