/* ====== home Section ====== */
header{
    display: block;
    position: relative;
    height: 100vh;
    padding-top: 7rem;
    overflow:hidden;
    text-align: center;
}
  
header::after {
    content: "";
    background: url("https://www.teahub.io/photos/full/309-3090562_neon-sun-wallpaper-4k.jpg")center;
    background-size: cover;
    opacity: 0.5;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;   
  }
/* SOCIAL ICONS */
.home__social a:hover{
    color: 1px solid var(--color-light);
}
.home__social{
    display:flex;
    justify-content: center;
    gap:3rem;
}
.home__social a{
    transform: translateX(-200%);
    animation: slideLeft 1s forwards;
    color: var(--color-light);
    text-decoration: none;
    font-size: 1.5rem;
    animation: slideDown 1s forwards;
    margin-top: 40px;
    transition: var(--transition);
}
/* Main Content home */
.home h2{
    font-size: 2.5rem;
    color: var(--color-light);
}

.home h5{
    transform: translateY(-100%);
    transition: var(--transition);
    animation: slideUp 1s forwards;
    color: var(--color-light);
    font-size: 1rem;
}
/* BUTTON DOWNLOADING */
.home__btn{
    display: flex;
    justify-content: center;
}
.btn__download {
    animation: slideDown 1s forwards;
    margin: 50px 0 0 0 ;
    text-align: center;
    box-sizing: border-box;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    background-color: transparent;
    border: 2px solid #e74c3c;
    border-radius: 0.6em;
    color: #e74c3c;
    cursor: pointer;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-self: center;
        -ms-flex-item-align: center;
            align-self: center;
    font-size: 1rem;
    line-height: 1;
    padding: 1.2em 2.8em;
    text-decoration: none;
    text-align: center;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
  }

  .btn__download a{
    color: var(--color-light);
}
.btn__download:hover a, .btn__download:focus a{
    color: var(--color-white);
  }
.btn__download{
    border-color: var(--color-primary);
    color: #fff;
    box-shadow: 0 0 40px 40px var(--color-primary) inset, 0 0 0 0 var(--color-primary);
    -webkit-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
  }



/* ====== Animations ========= */
@keyframes slideUp{
    0%{
        transform: translateY(-100%);
    }
    100%{
        transform: translateY(0%);
    }
}
@keyframes slideDown{
    0%{
        transform: translateY(100%);
    }
    100%{
        transform: translateY(0%);
    }
}


/* ============ Animation PRINT ========== */

@media screen and (min-height: 600px){
    .home h5{
        margin-top:150px ;}
    .home .wrapper{
        display: flex;
        justify-content: center;
    }
    .wrapper h2{
        width: 21ch;
        color: var(--color-white);
        font-family: monospace;
        white-space: nowrap;
        overflow: hidden;
        border-right: 4px solid var(--color-white);
        animation: cursor 0.75s step-end infinite, print 3s steps(21);
        transition: var(--transition);
    }
    @keyframes cursor{
        0%{
            border-color: var(--color-white);
        }
        50%{
            border-color: transparent;
        }
        100%{
            border-color: var(--color-white);
        }
    }

    @keyframes print{
        0%{
            width: 0;
        }
    }
}
/* SMALL */
@media screen and (max-width:600px) {
    .home h2{
        font-size: 2rem;
        border-right:none;
        transform: translateX(-200%);
        animation: h2Small 1s forwards;
        transition: var(--transition);
    }
    @keyframes h2Small {
        0%{
            transform: translateX(-200%);
        }
        100%{
            transform: translateX(0%);
        }
    }
    .home h5{
        font-size: 0.9rem;
    }
    .home .btn__download{
        font-size: 0.7rem;
        padding: 15px 20px;
        margin-top:10px ;
    }
    .home .home__social a{
        font-size: 1rem;
    }
    .home .home__social {
        gap: 1.3rem;
    }
}