.about {
    display: grid;
    grid-template-columns: 35% 50%;
    gap:15%;
}
/* IMAGE */
.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    display: grid;
    place-items: center;
}

.about__me-image{
    overflow: hidden;
    transition: var(--transition);
}

/* TITLE */

.about__content h2{
    font-size: 3rem;
    font-weight: bold;
}

/* ITEMS */
.about__item-in h5{
    font-size: 1rem;
}
.about__item-in span{
    color: var(--color-primary);
    font-size: 1.2rem;
}
.about__item-in{
    display: flex;
    gap: 0.5rem;
}
.about__item{
    margin: 20px 0 ;
}
.about__item-out p{
    font-size: 0.9rem;
    color: var(--color-light);
}



/* ======== MEDIA (MEDIUM) */
@media screen and (max-width: 1024px){
    .about{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
}
@media screen and (min-width: 800px){
    .about {
        padding: 150px 0;
    }
}
/* ======== MEDIA (SMALL) */
@media screen and (max-width: 600px){
    .about__me{
        width: 100%;
        margin: 0 auto 3rem;
    }
    .about__content h2{
        font-size: 2rem;
    }

    .about__content{
        text-align: left;
    }

    .about__content p{
        margin: 1.5rem 0;
    }
}




