nav{
    background: rgba(0,0,0,0.3);
    position: fixed;
    width: max-content;
    padding: 0 1.5rem 0 1.5rem;
    z-index: 3;
    display: block;
    left: 50%;
    transform: translateX(-50%);
    top: 2rem;
    gap: 0.8rem;
    display: flex;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a:hover{
    color: var(--color-primary);
    border-bottom: 1px solid var(--color-primary);
}

nav a.activee{
    color: var(--color-primary);
    border-bottom: 1px solid var(--color-primary);
}



nav a{
    width: 100px;
    padding: 1rem 1rem 0.3rem 1rem;
    display: block;
    font-size: 1.1rem;
    /* border-radius: 50%; */
    text-align: center;
    background: transparent;
    color: var(--color-light);
    border:1px solid transparent;
    transition: var(--transition);
}

@media screen and (max-width:644px){
  nav{
    background: rgba(0,0,0,0.3);
    position: fixed;
    width: max-content;
    padding: 0;
    z-index: 3;
    display: flex;
    left: 50%;
    transform: translateaX(-50%);
    top: 1rem;
    gap: 0.8rem;
    border-radius: 0;
    backdrop-filter: blur(15px);
}nav a{
  width: 50px;
  padding: 0.3rem 0;
  display: block;
  font-size: 1rem;
  /* border-radius: 50%; */
  text-align: center;
  background: transparent;
  color: var(--color-light);
  border:1px solid transparent;
  transition: var(--transition);
}
}