.container.contact__container{
    width: 50%;
}
#contact{
    margin-top:15rem ;}
/* btn submit */

.btn-submit{
    background: var(--color-primary);
    color: var(--color-bg);
    padding: 15px 25px;
    width: max-content;
    transition: var(--transition);
    border: 1px solid var(--color-primary);
}
.btn-submit:hover{
    background: transparent;
    color: var(--color-primary);
}
/* FORM */

form{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

form h5{
    font-size: 1rem;
}

#contact h2{
    font-size: 2rem;
    margin: 5rem 0;
    color: var(--color-primary);
}
textarea:hover{
    border-left: 2px solid var(--color-dop);
}
input:hover{
    border-left: 2px solid var(--color-dop);
}
input, textarea{
    transition:var(--transition);
    width: 100%;
    padding: 1.5rem;
    background: var(--color-primary-variant);
    border-left: 2px solid var(--color-primary);
    resize: none;
    color: var(--color-light);
    letter-spacing: normal;
    word-spacing: normal;
    line-height: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    text-align: start;
    appearance: auto;
    -webkit-rtl-ordering: logical;
    cursor: text;
}

#NumText{
    display: flex;
}
/* MEDIA */
@media screen and (max-width:1024px) {
    .container.contact__container{
        width: var(--container-width-md);
    }
}
/* SMALL */
@media screen and (max-width:800px) {
    .container.contact__container{
        width: var(--container-width-sm);
    }
}


